nav {
    height : 80px;
    background : rgb(63,167,90);
    margin :10px;
    border-radius: 40px;
}

.liste {
    list-style-type: none;
    background : rgb(63,167,90);
    height: 100%;
    display: flex;
    justify-content:end;
    align-items: center;
    border-radius: 40px;
    position: relative;
    margin-right: 20px;


}

.items {

    margin-right: 10px;
    align-items: center;
    color: white;
    cursor: pointer;
    float: right;
}

.btn{
    
    display: none;
}

.logo_blanc{

    float: left;
    margin-top: 10px;
    margin-left: 30px;
}

@media screen and (max-width: 500px){

    .liste{
        flex-direction: column;
        height: 200px;
        margin: 0;
        padding: 0;
    }

    .items:nth-child(1){
        border-top: 1px solid #fff;
        margin-top: 50px;
    }

    .items{
        height: 50px;
        width: 100%;
        border-bottom: 1px solid #fff;
        line-height: 50px;
        padding-left: 305px;

    }

    .btn{

        display: block;
        position: absolute;        
        right: 30px;
        top: 35px;
        background-color: rgb(63,167,90);;
        cursor: pointer;

    }

    .logo_blanc{

        display: none;
        
    }
       
          
}